<template>
    <v-container class="pa-2" fluid>
        <v-navigation-drawer v-model="drawer" absolute temporary color='rgba(50,50,50, 0.7)'>
        </v-navigation-drawer>        
        <v-alert type="error" border="left" color="#ff4444" v-if="showalert">
            This shit is not returning anything.
        </v-alert>
        <v-row>
            <transition-group name="fade" tag="span" style="width:100%">
                <v-col v-for="item in items" :key="item.coinid">
                    <v-card :color="item.color">
                        <v-list-item three-line>
                            <v-list-item-avatar size="50" tile>
                                <v-img :src="item.src"></v-img>
                            </v-list-item-avatar>
                            <v-list-item-content>
                                <div class="txt">
                                    <div class="coin">{{item.coin}}</div>
                                    <div class="stats">{{item.price}}&nbsp;&nbsp;<span
                                            :class="item.fclr">({{item.changein}}%)</span></div>
                                    <div class="profit" v-if="item.show">VALUE ${{item.profit}}</div>
                                </div>
                            </v-list-item-content>
                        </v-list-item>
                        <div class="footer">
                            <div>#{{item.rank}}</div>
                            <div>${{item.volume}}</div>
                        </div>
                    </v-card>
                </v-col>
            </transition-group>
        </v-row>
        <div style="padding-bottom:72px"></div>
        <v-btn fixed dark fab bottom small right color="pink" style="margin-bottom:-7px;" @click.prevent="pageRefresh">
            <v-icon>mdi-refresh</v-icon>
        </v-btn>
        <div class="lst-update">{{lastupdate}}</div>
        <v-footer fixed class="portfolio" color="#2E3032" height="55">
            <div @click.stop="drawer = !drawer">
                <div style="color:#2982DF; font-size:20px;">
                    <strong>VALUE: ${{this.portfolio.toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}</strong>
                </div>
                <div style="color:white; font-size:smaller;">
                    TOTAL MARKET: ${{this.totalmarket}}
                </div>
            </div>
        </v-footer>
    </v-container>
</template>

<script>
export default {
    data: () => ({
        items: [],
        hold: {
                "Electroneum":
                {
                    name: "Electroneum",
                    quantity: 210805.03,
                    imageurl: 'https://seeklogo.com/images/E/electroneum-etn-logo-2AD391D8F7-seeklogo.com.png',
                    lstname: 'Electroneum (ETN)',
                    color: '#0F1317',
                    coinid:1
                },
                "TomoChain":
                {
                    name: "TomoChain",
                    quantity: 2020.103,
                    imageurl: 'https://s2.coinmarketcap.com/static/img/coins/200x200/2570.png',
                    lstname: 'TomoChain (TOMO)',
                    color: '#0F1317',
                    coinid:2
                },
                "Bitcoin":
                {
                    name: "Bitcoin",
                    quantity: 0,
                    imageurl: 'https://s2.coinmarketcap.com/static/img/coins/200x200/1.png',
                    lstname: 'Bitcoin (BTC)',
                    color: '#0F1317',
                    coinid:3
                },
                "Ethereum":
                {
                    name: "Ethereum",
                    quantity: 0,
                    imageurl: 'https://s2.coinmarketcap.com/static/img/coins/200x200/1027.png',
                    lstname: 'Ethereum (ETH)',
                    color: '#0F1317',
                    coinid:4
                }            
        },
        portfolio: 0,
        totalmarket: '',
        decimals:5,
        showalert:false,
        lastupdate:'',
        drawer: null       
    }),
    mounted() {
        this.getAPIData();
    },
    methods: {        
        pageRefresh(){
            document.location.reload();
        },        
        currency(t, e) {
            e = Math.pow(10, e);
            for (var a = ["K", "M", "B", "T"], i = a.length - 1; i >= 0; i--) {
                var r = Math.pow(10, 3 * (i + 1));
                if (r <= t) {
                    1e3 == (t = Math.round(t * e / r) / e) && i < a.length - 1 && (t = 1, i++), t += "" + a[i];
                    break
                }
            }
            return t
        },
        dtconvert(dtstr){
            let dt = new Date(dtstr);
            var options = {day:"numeric",month:"numeric",year:"2-digit", hour:"numeric", minute:"numeric", second:"numeric",hour12: false};
            return `${dt.toLocaleDateString('en-US', options)}`.replace(',','');
        },
        getAPIData(){
            this.axios.get(`https://finarofunc.azurewebsites.net/api/coinlistdata`).then((response) => {
                let data = response.data.data;
                // eslint-disable-next-line
                for (let [k,v] of Object.entries(data)) {
                    let el = this.hold[v.name];
                    this.items.push({
                        color: el.color,
                        src: el.imageurl,
                        coin: el.lstname,
                        price: parseFloat(v.quote.USD['price']).toFixed(this.decimals),
                        fclr: Number(v.quote.USD['percent_change_24h']) < 0 ? 'loss' : 'gain',
                        changein: Number(v.quote.USD['percent_change_24h']),
                        profit: parseFloat(v.quote.USD['price']).toFixed(this.decimals) * el.quantity,
                        rank: v.cmc_rank,
                        volume: this.currency(v.quote.USD['volume_24h'], 2),
                        coinid:el.coinid,
                        show:el.quantity>0
                    });
                    this.portfolio = this.portfolio + (parseFloat(v.quote.USD['price']).toFixed(this.decimals) * el.quantity);               
                }
            });

            this.axios.get(`https://finarofunc.azurewebsites.net/api/coinmarketdata`).then((response) => {
                let data = response.data.data;
                this.totalmarket = this.currency(data.quote.USD["total_market_cap"],2);
                this.showalert = (this.totalmarket == 0);
                this.lastupdate = this.dtconvert(data.quote.USD["last_updated"]);
            });
        }
    }
}
</script>

