}

.footer{
  justify-content: space-between;
  display: flex;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom:5px;
  font-family: 'Roboto';
  color:#6D7579;
}

.portfolio{
  font-family: 'Roboto';
  padding-left:14px;
}

